@use "sass:map";
@use "sass:meta";
@use "@pkg/react-storybook/Bundle/Theme";

$props: (
    justify: (justify-content, (
        start: flex-start,
        end: flex-end,
        center: center,
        between: space-between,
        around: space-around,
        evenly: space-evenly,
    )),
    align: (align-items, (
        default: stretch,
        start: flex-start,
        end: flex-end,
        center: center,
        base: baseline,
    )),
    wrap: (flex-wrap, (
        default: nowrap,
        wrap: wrap,
        reverse: wrap-reverse,
    )),
    grow: (flex-grow, (
        default: auto,
        none: 0,
        alpha: 1,
        beta: 2,
        gamma: 3
    )),
    shrink: (flex-shrink, (
        default: initial,
        none: 0,
        alpha: 1,
        beta: 2,
        gamma: 3
    )),
    dir: (flex-direction, (
        default: row,
        column: column,
        row-reverse: row-reverse,
        column-reverse: column-reverse,
    )),
);

$presets: (
    default: (
        justify: default,
        align: default,
        wrap: default,
    )
);

@mixin set($preset: null, $args...) {
    $data: Theme.data-parse(meta.keywords($args), $preset, $props, $presets);
    display: flex;
    @include Theme.render(map.get($data, schema));
}
